.main {
  @include flex-box-center(column);
  width: 100%;
  height: 100%;
}

.error-notification {
  @include flex-box-center(row);
  width: 100%;
  background: rgba(255, 0, 0, 0.2);
  padding: .5rem;
  border: 1px solid rgba(255, 0, 0, 0.8);
  border-radius: 1rem;
  margin: .24rem;
  .error-msg {
    margin-right: auto;
    padding-left: $global-spacer;
  }
}


